.container {
    font-size: 1.5rem;
    max-width: 50rem;
    margin: 0 auto;
    padding: 4rem 2rem;
    text-align: center;
    animation: fadeIn 0.8s ease-in;
    display: flex;
    flex-direction: column;
    position: relative;
}

.orvLogoContainer {
    position: absolute;
    left: -14rem;
    top: 10rem;
    opacity: 0;
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.orvLogoContainer:hover {
    opacity: 1;
}

.orvLogo {
    width: 12.5rem; 
    height: auto; 
    display: block; 
    margin: 0 auto;
    border-radius: 0.5rem;
}



.heading {
    color: #2D3748;
    margin-bottom: 2rem;
    position: relative;
    display: inline-block;
    width: 100%;
    text-align: center;
}

.contentWrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
}

.imageContainer {
    flex: 0 0 auto;
    margin-right: 2rem;
}

.image {
    max-width: 12.5rem;
    height: auto;
    border-radius: 0.5rem;
}

.description {
    flex: 1;
    line-height: 1.8;
    font-size: 1.25rem;
    color: black;
    text-align: left;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(1.25rem);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@media (max-width: 768px) {
    .container {
        font-size: 1rem;
        padding: 1.2rem 0.5rem;
        max-width: 100vw;
    }
    .heading {
        font-size: 1.2rem;
        margin-bottom: 1rem;
    }
    .contentWrapper {
        flex-direction: column;
        gap: 1rem;
    }
    .imageContainer {
        margin-right: 0;
        margin-bottom: 1rem;
    }
    .image {
        max-width: 8rem;
    }
    .description {
        font-size: 1rem;
        text-align: center;
    }
    .orvLogo {
        width: 7rem;
    }
}